<template>
  <div>
    <div class="miniheader">
      <div class="container">
        <div class="miniheader-logo">
          <router-link to="/">小米官网</router-link>
        </div>
        <div class="miniheader-title">
          <h2>确认订单</h2>
          <p>温馨提示：产品是否购买成功，以最终下单为准哦，请尽快结算</p>
        </div>
        <div class="miniheader-info">
          <a href="#" class="user-name">
            <span>{{user.name}}</span>
          </a>
          <span class="sep">|</span>
          <a href="javascript:;" class="user-name" @click="confirmQuit">
            <span>注销</span>
          </a>
          <!-- <span class="sep">|</span>
          <a href="javascript:;" class="info-order">我的订单</a>-->
        </div>
      </div>
    </div>

    <div class="checkout">
      <div class="container">
        <div class="checkout-box">
          <div class="checkout-address clearfix">
            <p class="address-header">收货地址</p>
            <div class="address-body">
              <!-- 有select时候才会修改字样 -->
              <div
                class="address-item"
                :class="{select:item.address_id==selectAddress}"
                v-for="(item,id) in addressList"
                :key="id"
                @click="selectAddress=item.address_id"
              >
                <dl>
                  <dt class="recipient">{{item.recipient}}</dt>
                  <dd class="phone">{{item.phone}}</dd>
                  <dd class="area">{{item.area}}</dd>
                  <dd class="address">{{item.address}}</dd>
                </dl>
                <div class="actions" @click="updateAddress(item)">
                  <a href="javascript:;">修改</a>
                </div>
              </div>

              <div class="address-item address-new" @click="newAddressVisible=true">
                <i class="iconfont">&#xe609;</i>
                添加新地址
              </div>
            </div>
          </div>
          <div class="checkout-goods">
            <p class="goods-header">商品</p>
            <div class="goods-body">
              <ul class="goods-list clearfix">
                <li class="clearfix">
                  <div class="goods-img"></div>
                  <div class="goods-name">商品名称</div>
                  <div class="goods-price">价格</div>
                  <div class="goods-total">总价</div>
                </li>
                <li class="clearfix" v-for="(item,idx) in proArr" :key="idx">
                  <div class="goods-img">
                    <img :src="item.imgs[0]" alt>
                  </div>
                  <div class="goods-name">
                    <router-link :to="`/product/${item.pid}`">{{item.name}}</router-link>
                  </div>
                  <div class="goods-price">{{item.price}}元</div>
                  <div class="goods-total">{{item.price*item.quantity}}元</div>
                </li>
              </ul>
            </div>
          </div>
          <div class="checkout-count clearfix">
            <div class="count-box">
              <ul>
                <li>
                  <label>商品件数：</label>
                  <span>
                    <span class="total-quantity">{{proArr.length}}</span>件
                  </span>
                </li>
                <li>
                  <label>商品总价：</label>
                  <span class="total-price">
                    <span class="price">{{productTotalPrice}}</span>元
                  </span>
                </li>
                <li>
                  <label>活动优惠：</label>
                  <span>-0元</span>
                </li>
                <li>
                  <label>优惠券抵扣：</label>
                  <span>-0元</span>
                </li>
                <li>
                  <label>运费：</label>
                  <span>0元</span>
                </li>
                <li class="total-price">
                  <label>应付总额：</label>
                  <span>
                    <em class="price">{{productTotalPrice}}</em>元
                  </span>
                </li>
              </ul>
            </div>
          </div>
          <div class="checkout-bar clearfix">
            <div class="bar-btn">
              <a href="javascript:;" id="checkout" @click="goToCheckout">去结算</a>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="edit-bg" v-if="newAddressVisible"></div>
    <div class="edit-address" v-if="newAddressVisible">
      <div class="edit-header">
        <span class="title">添加收获地址</span>
        <a href="javascript:void(0);" class="close">
          <i class="iconfont">&#xe602;</i>
        </a>
      </div>
      <div class="edit-body">
        <form class="body-form clearfix">
          <input type="hidden" name="address_id">
          <!--隐藏的address_id,当编辑地址并向后台发送数据时，需要用到-->
          <div class="form-section form-name">
            <!-- <label for="user_name">姓名</label> -->
            <input
              type="text"
              name="recipient"
              class="input-text name-input"
              v-model="newAddress.recipient"
              placeholder="收货人姓名"
            >
          </div>
          <div class="form-section form-phone">
            <!-- <label for="user_phone">手机号</label> -->
            <input
              type="text"
              name="phone"
              class="input-text phone-input"
              v-model="newAddress.phone"
              placeholder="手机号"
            >
          </div>
          <div class="form-section form-area">
            <input
              type="text"
              name="area"
              class="input-text area-input"
              v-model="newAddress.area"
              placeholder="选择省 / 市 / 区 / 街道"
            >
          </div>
          <div class="form-section form-detail">
            <!-- <label for="user_detail">详细地址</label> -->
            <textarea
              type="textarea"
              name="address"
              class="input-text detail-input"
              placeholder="详细地址，路名或街道名称，门牌号"
              v-model="newAddress.address"
            ></textarea>
          </div>
        </form>

        <div class="form-select" style="display:none">
          <span class="select-close">x</span>
          <div class="select-box clearfix">
            <div class="select-item" data-init-txt="选择省份/自治区">选择省份/自治区</div>
            <div class="select-item hidden" data-init-txt="选择城市/地区">选择城市/地区</div>
            <div class="select-item hidden" data-init-txt="选择区县">选择区县</div>
            <!-- <div class="select-item hidden" data-init-txt="选择配送区域">选择配送区域</div> -->
          </div>
          <div class="option-box">
            <ul class="option-list clearfix province">
              <li class="addr-tpl" data-value data-txt></li>
            </ul>
            <ul class="option-list clearfix hidden city"></ul>
            <ul class="option-list clearfix hidden district"></ul>
          </div>
        </div>
      </div>
      <div class="edit-footer">
        <a href="javascript:void(0);" class="btn btn-primary confirm" @click="addNewAddress">保存</a>
        <a href="javascript:void(0);" class="btn btn-gray close" @click="resetAddressInput">取消</a>
      </div>
    </div>
  </div>
</template>

<script>
import api from "../../utils/api";
import { mapState, mapActions } from "vuex";
export default {
  mounted() {
    //   先登录
    // this.checklogin()
    //   .then(data => {
    //     console.log("现在是登录状态");
        // 获取商品列表
        this.ids = this.$route.params.ids;

        api.get("/user/cart?ids=" + this.ids).then(data => {
          console.log(data);
          this.proArr = data;
        });
        // 获取地址
        api.get("/user/addressList").then(data => {
          this.addressList = data;
        });
    //   })
    //   .catch(() => {
    //     console.log("现在是没登录状态");
    //   });
  },
  data() {
    return {
      proArr: [],
      addressList: [],
      newAddressVisible: false, //添加地址窗口可见否
      selectAddress: "", //选择的地址
      newAddress: {
        recipient: "",
        phone: "",
        area: "",
        address: "",
        address_id: ""
      },
      ids: ""
    };
  },
  computed: {
    productTotalPrice() {
      return this.proArr.reduce(
        (sum, item) => sum + item.price * item.quantity,
        0
      );
    },
    ...mapState("user", ["user"])
  },
  methods: {
    ...mapActions("user", ["logout"]),
    addNewAddress() {
      console.log("提交新地址");
      //   更新还是插入？
      api.post("/user/updateAddress", this.newAddress).then(data => {
        // console.log(data);
        if (this.newAddress.address_id) {
          //更新
          const index = this.addressList.findIndex(
            el => el.address_id == this.newAddress.address_id
          );
          this.$set(this.addressList, index, data);
        } else {
          //新增

          this.addressList.unshift(data);
        }
        this.resetAddressInput();
      });
    },
    resetAddressInput() {
      // 所有输入框清空。
      this.newAddress = {};
      this.newAddressVisible = false;
    },
    updateAddress(item) {
      this.newAddressVisible = true;
      this.newAddress = {
        recipient: item.recipient,
        phone: item.phone,
        area: item.area,
        address: item.address,
        address_id: item.address_id
      };
    },
    goToCheckout() {
      if (!this.selectAddress) {
        alert("请选择收货地址");
        return;
      }

      api
        .post("/user/checkout", {
          address_id: this.selectAddress,
          ids: this.ids
        })
        .then(data => {
          this.$router.push("/confirm/" + data); //order_no
        });
    },
    confirmQuit() {
      this.$confirm("确认退出登录？")
        .then(_ => {
          this.logout();
          this.$router.push("/login");
        })
        .catch(_ => {});
    }
  }
};
</script>

<style>
@import url("../css/style.css");
@import url("../css/iconfont.css");
@import url("../css/checkout.css");
</style>
